
ul {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
}
.box {
  padding: 46px 20px 50px;
}
.box header {
  font: 30px/2 "微软雅黑";
}
.box span {
  color: green;
}
.box .detailBox {
  color: #999;
  line-height: 40px;
  overflow: hidden;
}
.box .detailBox .left {
  float: left;
  width: 50%;
  padding-top: 15px;
}
.box .detailBox img {
  float: right;
  width: 40%;
}
.box .Collection {
  background: none;
  border: 1px solid orange;
  border-radius: 5px;
  color: orange;
  font-size: 20px;
  padding: 3px 30px;
}
.box .works {
  padding-top: 50px;
  overflow: auto;
}
.box .works .listBox {
  overflow: hidden;
}
.box .works .listBox li {
  float: left;
  width: 150px;
  text-align: center;
}
.box .works .listBox li img {
  width: 120px;
  height: 170px;
}



