
ul{
	padding: 0;
	margin: 0;
}
li{
	list-style: none;
}
/*weui-cells*/
.rater.weui-cell{
	padding: 0;
	text-align: center;
}
#box .weui-cells{
	margin: 0;
}
.hot {
	overflow: hidden;
}
.hot .weui-cells{
	font-size: 0px;
}
.hot li{
	text-align: center;
	float: left;
	width: 33.333%;
	height: 180px;
	padding-top: 10px;
	/*background-color: #0ff;*/
	
	/*border: 1px solid #000;*/
	/*box-sizing: border-box;*/
}
.hot li img{
	width: 90px;
	height: 130px;
}
.hot .weui-cells__title{
	display: inline-block;
	padding: 0;
}
.hot .weui-cells__title{
	margin: 0;
}
.hot .title{
	display: block;
	/*background-color: #f00;*/
	font: 14px/16px "微软雅黑";
}
.hot .tempNo{
	margin-top: 3px;
	font-size: 12px;
	display: inline-block;
	color: #ccc;
}
.hot .weui-cells.vux-no-group-title:before,
.hot .weui-cells.vux-no-group-title:after{
	display: none;
}
.ratingGroup {
	font-size: 0;
	margin-top: 5px;
}
.ratingNum{
	font-size: 12px;
}



