
ul{
	padding: 0px;
	margin: 0;
	list-style: none;
}
.Collection{
	background: none;
	border: 1px solid orange;
	border-radius: 5px;
	color: orange;
	font-size: 20px;
	padding: 3px 30px;
}
.title{
	display: block;
	font:  20px/40px "微软雅黑";
}
.originalTitle{
	color: #ccc;
	display: block;
}
.movieBox{
	padding: 70px 20px 60px;
}
.contentLeft{
	float: left;
	width: 50%;
	font: 14px/30px "微软雅黑";
	color: #666;
}
.year{
	display: block;
}
.contentLeftRight{
	padding-top: 30px;
	float: right;
	width: 40%;
}
.imgBox{
	overflow: hidden;
}
.summary{
	padding-top: 20px;
}
.summary p{
	word-wrap: break-word;
	color: #999;
	font-size: 16px;
}
.summary .summaryText{
	padding-top: 15px;
	color: #000;
	font: 16px/30px "微软雅黑";
}
/*._v-container{
	box-sizing: border-box;
	
	padding-top: 46px;
}	
._v-content{
	padding-bottom: 50px;
}*/
.more{
	float: right;
	color: #09BB07;
}
.wishCount{
	padding-top: 15px;
}
.wish,
.already{
	display: inline-block;
	padding: 2px;
	border: 1px solid orange;
	color: orange;
}
.casts{
	padding-top: 20px;
}
.xBox{
	width: 100%;
	overflow: auto;
}
.casts ul{
}
.casts h3{
	color: #777;
}
.casts li{
	width: 100px;
	float: left;
	text-align: center;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	/*border: 1px solid #000;*/
}
.casts img{
	width: 85px;
	height: 121px;
}
.xBox li .name{
	font: 14px/1.2 "微软雅黑";
	color: #333;
}



